<template>
  <chart-card title="一年以上账龄" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：万元</span>
        <el-row style="margin: 0;height: 60%;width: 100%;">
          <div :id="refName" :ref="refName" style="height: 100%;" />
        </el-row>
        <el-row style="margin: 0;height: 40%;width: 100%;">
          <el-table
            :data="tableData"
            style="width: 100%;"
            class="my-table"
            border
            :show-header="false"
            :cell-style="handleCellStyle"
          >
            <el-table-column
              align="center"
              prop="col0"
              label=""
            />
            <el-table-column
              align="center"
              prop="col1"
              label="1月"
              :formatter="row => `${row.col1}`"
            />
            <el-table-column
              align="center"
              prop="col2"
              label="2月"
              :formatter="row => `${row.col2}`"
            />
            <el-table-column
              align="center"
              prop="col3"
              label="3月"
              :formatter="row => `${row.col3}`"
            />
            <el-table-column
              align="center"
              prop="col4"
              label="4月"
              :formatter="row => `${row.col4}`"
            />
            <el-table-column
              align="center"
              prop="col5"
              label="5月"
              :formatter="row => `${row.col5}`"
            />
            <el-table-column
              align="center"
              prop="col6"
              label="6月"
              :formatter="row => `${row.col6}`"
            />
            <el-table-column
              align="center"
              prop="col7"
              label="7月"
              :formatter="row => `${row.col7}`"
            />
            <el-table-column
              align="center"
              prop="col8"
              label="8月"
              :formatter="row => `${row.col8}`"
            />
            <el-table-column
              align="center"
              prop="col9"
              label="9月"
              :formatter="row => `${row.col9}`"
            />
            <el-table-column
              align="center"
              prop="col10"
              label="10月"
              :formatter="row => `${row.col10}`"
            />
            <el-table-column
              align="center"
              prop="col11"
              label="11月"
              :formatter="row => `${row.col11}`"
            />
            <el-table-column
              align="center"
              prop="col12"
              label="12月"
              :formatter="row => `${row.col12}`"
            />
          </el-table>
        </el-row>
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryAgeChartApi } from '@/apis/modules/panel/zhongyuan'
import _ from 'lodash'

export default {
  name: 'AccountAgeInfo',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'AccountAgeInfo',
      tableData: []
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryAgeChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const { series, xAxis } = data
              const colors = ['#2ec7c9', '#b6a2de', '#5ab1ef', '#ffb980', '#d87a80']
              this.tableData = _.cloneDeep(series).map((c, i) => {
                c.data.unshift(c.name)
                const obj = {}
                c.data.forEach((b, k) => {
                  obj[`col${k}`] = b
                })
                obj.color = colors[i]
                return obj
              })
              this.getCompleteOptions(null, series, xAxis)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
      * @legendData
      * @series
      * @xAxis
      */
    getCompleteOptions(legendData, series, xAxis) {
      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          // left: '3%',
          left: '1%',
          // right: '4%',
          right: '0%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    },
    handleCellStyle({ row, column, rowIndex, columnIndex }) {
      let obj = {}
      if (columnIndex === 0) {
        obj = { color: row.color }
      }
      return obj
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
::v-deep .my-table {
  // margin-left: 30px;
  .el-table__cell {
    padding: 2px 0;
    .cell {
      padding: 0 0;
    }
  }
}
</style>

