<template>
  <el-dialog
    :title="title"
    top="5vh"
    width="40%"
    :modal="false"
    :visible="value"
    :show-close="true"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    custom-class="notice-diag01"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <!-- 展示看板增加实时出入库（记录刚入库或刚出库的物料以及数量）展示框根据物料数量自适应大小。物料名称过长的物料换行展示 -->
    <template v-if="dlgData.length">
      <div v-for="(item, index) in dlgData" :key="index" class="diag-wrap">
        <el-tag effect="dark" size="medium" style="font-size: 18px;">{{ item.orderTypeText }}</el-tag>
        <div class="diag-main">
          <div class="title">{{ `${item.orderTypeText}单据 ${item.billNumber} ${item.orderTypeText}完成` }}</div>
          <br>
          <div v-for="(c, i) in item.list" :key="i" class="item">
            <span>{{ c.materialCode }}</span>
            <span class="material-name">{{ c.materialName }}</span>
            <span>{{ `数量：${c.countNum}` }}</span>
            <span>{{ `已${item.orderTypeText}` }}；</span>
          </div>
        </div>
      </div>
    </template>
    <template v-else>
      <div class="empty-txt">当前暂无出入库信息，弹窗将在 10S 关闭！</div>
    </template>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'

export default {
  name: '',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => {}
    },
    dlgData: {
      type: Array,
      default: () => ([])
    }
  },
  dicts: [],
  data() {
    return {
    }
  },
  computed: {
  },
  methods: {
    init() {
    },
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
      this.$emit('resetDlgData')
    },
    initSelect() {

    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .notice-diag01 {
  /* 去除边框和阴影等默认样式 */
  border: none;
  box-shadow: none;
  background-color: rgba(0, 0, 0, 0.3);
}
::v-deep .el-dialog__header {
  display: none;
}
// ::v-deep .el-dialog__body {
// }
.diag-wrap {
  display: flex;
  margin-bottom: 15px;
}
// .diag-type {
//   color: #FFF;
//   background-color: #409eff;
//   border-radius: 5px;
//   padding: 5px 15px;
//   font-size: 18px;
//   height: 35px;
//   line-height: 35px;
// }
.diag-main {
  color: #FFF;
  flex-grow: 2;
  display: flex;
  flex-flow: column wrap;
  .title {
    font-size: 16px;
    text-align: center;
  }
  .item {
    width: 100%;
    padding: 5px;
    font-size: 14px;
    span {
      display: inline-block;
      padding: 5px 10px;
    }
  }
}
.empty-txt {
  color: #FFB980;
  line-height: 45px;
  text-align: center;
  font-size: 23px;
}
</style>
