<template>
  <chart-card title="近3年物资消耗变化" class="chart-card">
    <template>
      <div style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：万元</span>
        <div :id="refName" :ref="refName" v-loading="loading" style="height: 100%;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getLast3YearsMaterialDepleteChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'ConsumptionChange',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'ConsumptionChange',
      options: {}
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      // const charts = this.$refs[this.refName]
      // charts.showLoading({
      //   text: 'Loading…',
      //   color: '#4ea397',
      //   maskColor: 'rgba(255, 255, 255, 0.4)'
      // })
      this.loading = true
      return new Promise((resolve, reject) => {
        getLast3YearsMaterialDepleteChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const legendData = data.series.map(c => c.name)
              const series = data.series.map(c => ({ ...c, ...c.type !== 'bar' && { yAxisIndex: 1 }}))
              const xAxis = data.xAxis
              this.getCompleteOptions(legendData, series, xAxis)
            }
            this.loading = false
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
        * @legendData
        * @series
        * @xAxis
        */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.options)
      this.chart && this.chart.resize()
      this.loading = false
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
</style>

