<template>
  <chart-card title="主要物资占比" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：元</span>
        <el-row style="margin: 0;height: 70%;width: 100%;">
          <div :id="refName" :ref="refName" style="height: 100%;" />
        </el-row>
        <el-row style="margin: 0;height: 30%;width: 100%;">
          <el-descriptions direction="vertical" title="" :content-style="{textAlign: 'center'}" :label-style="{textAlign: 'center'}" :column="14" border>
            <el-descriptions-item v-for="(c,i) in tableData" :key="i" :label-style="{color: colors[i]}" :label="c.name">{{ c.value }}</el-descriptions-item>
          </el-descriptions>
        </el-row>
      </div>
    </template>
  </chart-card>
</template>
<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getMaterialPercentagePieChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'MaterialRatio',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'MaterialRatio',
      tableData: [],
      colors: [
        '#2ec7c9',
        '#b6a2de',
        '#5ab1ef',
        '#ffb980',
        '#d87a80',
        '#8d98b3',
        '#e5cf0d',
        '#97b552',
        '#95706d',
        '#dc69aa',
        '#07a2a4',
        '#9a7fd1',
        '#588dd5',
        '#f5994e',
        '#c05050',
        '#59678c',
        '#c9ab00',
        '#7eb00a',
        '#6f5553',
        '#c14089'
      ]
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getMaterialPercentagePieChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const series = [
                {
                  name: 'MaterialRatio',
                  type: 'pie',
                  label: {
                    show: true, // 显示标签
                    position: 'outside', // 在饼图内部显示标签
                    formatter: '{b}：{d}%' // 定义标签的显示格式，{b}代表数据项名称，{d}代表百分比
                  },
                  // center: ['53%', '55%'], // 饼图距离容器上下左右边界的调整使用grid调整无效,第一个数值表示左右位置，第二个数值表示上下位置
                  radius: ['40%', '70%'],
                  avoidLabelOverlap: false,
                  itemStyle: {
                    borderRadius: 10,
                    borderColor: '#fff',
                    borderWidth: 3
                  },
                  data,
                  emphasis: {
                    label: {
                      show: true,
                      fontSize: 12
                    }
                  }
                }
              ]
              this.tableData = data.map(c => ({
                ...c,
                value: c.value.toFixed(2)
              }))
              this.getCompleteOptions(null, series, null)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
        * @legendData
        * @series
        * @xAxis
        */
    getCompleteOptions(legendData, series, xAxis) {
      this.option = {
        // title: {
        //   text: '库存占比',
        //   left: 0,
        //   top: 0,
        //   textStyle: {
        //     fontSize: '18px',
        //     fontWeight: 800
        //   }
        // },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br />{b} : {c} ({d}%)'
        },
        legend: {
          data: legendData,
          left: 0,
          show: false
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
</style>

