<template>
  <chart-card title="库存占比" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：元</span>
        <el-row style="margin: 0;height: 100%;width: 100%;">
          <el-col :span="16" style="height: 100%;">
            <div :id="refName" :ref="refName" style="height: 100%;" />
          </el-col>
          <el-col :span="8" style="height: 100%;">
            <div class="text-wrap">
              <div v-for="(item, index) in tableData" :key="index" class="item-container">
                <span class="title" :style="{color: item.color}">{{ item.name }}</span>
                <div><span class="label">总额：</span><span class="content">{{ item.value }}</span></div>
              </div>
            </div>
            <!-- <el-descriptions title="" :content-style="{textAlign: 'center',width: '120px'}" :label-style="{textAlign: 'center',width: '70px'}" :column="1" border>
              <el-descriptions-item v-for="(item, index) in tableData" :key="index" :label="item.name">{{ item.value }}</el-descriptions-item>
            </el-descriptions> -->
          </el-col>
        </el-row>
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryPieChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'InventoryRatio',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'InventoryRatio',
      option: {},
      tableData: []
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryPieChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const series = [
                {
                  name: 'InventoryRatio',
                  type: 'pie',
                  label: {
                    show: true, // 显示标签
                    position: 'outside', // 在饼图内部显示标签
                    formatter: '{b}：{d}%' // 定义标签的显示格式，{b}代表数据项名称，{d}代表百分比
                  },
                  radius: '50%',
                  data,
                  emphasis: {
                    itemStyle: {
                      shadowBlur: 10,
                      shadowOffsetX: 0,
                      shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                  },
                  center: ['50%', '50%']
                }
              ]
              const colors = ['#2ec7c9', '#b6a2de', '#5ab1ef', '#ffb980']
              this.tableData = data.map((c, i) => ({ ...c, color: colors[i] }))
              this.getCompleteOptions(null, series, null)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br />{b} : {c} ({d}%)',
          position: function(pos, params, dom, rect, size) {
            // 鼠标在左侧时 tooltip 显示到右侧，鼠标在右侧时 tooltip 显示到左侧。
            var obj = { top: 60 }
            obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 5
            return obj
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
.text-wrap {
  display: flex;
  flex-flow: column wrap;
  .item-container {
    margin-bottom: 10px;
    display: flex;
    flex-flow: column wrap;
    .title {
      font-size: 16px;
    }
    .label {
      font-size: 14px;
    }
    .content {
      font-size: 12px;
    }
  }
}
</style>
