import SockJS from 'sockjs-client'
import Stomp from 'webstomp-client'
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
    }
  },
  mounted() {
    if ('WebSocket' in window) {
      this.connection()
    } else {
      alert('当前浏览器不支持 websocket')
    }
  },
  beforeDestroy() {
    this.destroy()
  },
  computed: {
    ...mapGetters({
      token: 'token',
      userInfo: 'userInfo'
    })
  },
  methods: {
    connection() {
      const socketUrl = `${process.env.VUE_APP_BASE_API_WMS}/StompSocket`
      const socket = new SockJS(socketUrl)
      const debug = process.env.NODE_ENV !== 'production'
      this.stompClient = Stomp.over(socket, { debug: debug })
      this.stompClient.connect({ Authorization: this.token }, (frame) => {
        console.log('%c连接成功了', 'color: #2ec7c9;font-size: 20px;')
        this.socketSubscribe()
      }, (err) => {
        console.log('%csocket连接失败了', 'color: #ffb980;font-size: 20px;', err)
        this.reconnect(socketUrl)
      })
    },
    // socket 订阅的消息列表
    socketSubscribe() {
      const url = `/topic/wms/stockInAndOutInfo`
      this.stompClient.subscribe(url, (greeting) => {
        console.log('greeting: ', greeting)
        if (greeting.body) {
          const info = JSON.parse(greeting.body)
          console.log(`%c您有新的消息`, 'color: #2ec7c9;font-size: 20px;', info)
          this.setNoticeData(info)
        }
      }, (err) => {
        console.log('%csocket连接出错了', 'color: #ffb980;font-size: 20px;', err)
      })
    },
    // socket 断线重连
    reconnect(socketUrl) {
      console.log('%csocket重连中', 'color: #5ab1ef;font-size: 20px;')
      let connected = false
      let connectTimes = 1
      const reconInv = setInterval(() => {
        console.info('in interval' + Math.random())
        const socket = new SockJS(socketUrl)
        const debug = process.env.NODE_ENV !== 'production'
        this.stompClient = Stomp.over(socket, { debug: debug })
        this.stompClient.connect({}, (frame) => {
          console.log('%csocket重连成功了', 'color: #2ec7c9;font-size: 20px;')
          // 连接成功，清除定时器
          clearInterval(reconInv)
          connected = true
          this.socketSubscribe()
        }, () => {
          // 连接失败 计数
          connectTimes++
          console.log(`%csocket重连次数为${connectTimes}次`, 'color: #ffb980;font-size: 20px;')
          if (connectTimes > 10) {
            clearInterval(reconInv)
            connected = false
            console.log(`%csocket重连次数超过${connectTimes}次，停止连接`, 'color: #ffb980;font-size: 20px;')
            this.$message({
              message: '用户消息接收失败，请刷新页面重试',
              type: 'error',
              duration: 2 * 1000
            })
          }
          console.log(`%csocket重连失败`, 'color: #ffb980;font-size: 20px;')
          console.info('connected:' + connected)
          if (connected) {
            console.info('connect .... what?')
          }
        })
      }, 2000)
    },
    destroy() {
      if (this.stompClient) {
        // 断开socket连接
        this.stompClient.disconnect(() => {
          console.log('%csocket断开连接了', 'color: #ffb980;font-size: 20px;')
        }, { Authorization: this.token })
      }
    }
  }
}
