<template>
  <div class="container">
    <div class="btn-control">
      <el-button :type="isPlaying ? 'success' : 'danger'" circle :icon="!isPlaying ? 'el-icon-video-play' : 'el-icon-video-pause'" @click="togglePlay" />
    </div>
    <el-carousel
      ref="carousel"
      class="carousel"
      direction="vertical"
      :interval="30 * 1000"
      :autoplay="isPlaying"
      indicator-position="none"
      arrow="never"
      @change="carouselChange"
    >
      <el-carousel-item>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="14" style="height: 100%;">
            <inventory-info style="height: 100%;" />
          </el-col>
          <el-col :span="8" style="height: 100%;">
            <inventory-ratio style="height: 100%;" />
          </el-col>
        </el-row>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="14" style="height: 100%;">
            <account-age-info style="height: 100%;" />
          </el-col>
          <el-col :span="8" style="height: 100%;">
            <account-age-ratio style="height: 100%;" />
          </el-col>
        </el-row>
      </el-carousel-item>
      <el-carousel-item>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="11" style="height: 100%;">
            <inventory-carry-rate style="height: 100%;" />
          </el-col>
          <el-col :span="11" style="height: 100%;">
            <imported-or-domestic-ratio style="height: 100%;" />
          </el-col>
        </el-row>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="11" style="height: 100%;">
            <material-consumption style="height: 100%;" />
          </el-col>
          <el-col :span="11" style="height: 100%;">
            <material-ratio style="height: 100%;" />
          </el-col>
        </el-row>
      </el-carousel-item>
      <el-carousel-item>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="11" style="height: 100%;">
            <average-inventory style="height: 100%;" />
          </el-col>
          <el-col :span="11" style="height: 100%;">
            <account-age style="height: 100%;" />
          </el-col>
        </el-row>
        <el-row type="flex" justify="space-around" style="height: calc(50vh - 30px);">
          <el-col :span="11" style="height: 100%;">
            <consumption-change style="height: 100%;" />
          </el-col>
          <el-col :span="11" style="height: 100%;">
            <carry-ratio style="height: 100%;" />
          </el-col>
        </el-row>
      </el-carousel-item>
    </el-carousel>
    <div class="control-btn">
      <el-button circle @click="prevSlide">
        <i class="el-icon-arrow-up" />
      </el-button>
      <el-button circle @click="nextSlide">
        <i class="el-icon-arrow-down" />
      </el-button>
    </div>
    <notice-dialog v-model="visible" :dlg-data="noticeData" @resetDlgData="resetDlgData" />
  </div>
</template>

<script>
import ChartComponents from './components' // 批量引入的chart组件
import NoticeDialog from './NoticeDialog' // 公告信息弹窗组件
import ws from './noticeMessage'

export default {
  components: {
    ...ChartComponents,
    NoticeDialog
  },
  mixins: [ws],
  data() {
    return {
      isPlaying: true,
      visible: false,
      noticeData: [],
      timer: null
    }
  },
  computed: {

  },
  watch: {
  },
  mounted() {
    this.$refs.carousel.handleMouseEnter = () => {}
  },
  methods: {
    carouselChange() {},
    delHandleMouseEnter() {
      this.$refs.carousel.handleMouseEnter = () => {}
    },
    // 播放 / 暂停
    togglePlay() {
      this.isPlaying = !this.isPlaying
    },
    prevSlide() {
      this.$refs.carousel.prev()
    },
    nextSlide() {
      this.$refs.carousel.next()
    },
    setNoticeData(data = undefined) {
      const obj = {
        ...data,
        orderTypeText: data.billNumber.indexOf('RK') !== -1 ? '入库' : '出库',
        list: data.billNumber.indexOf('RK') !== -1 ? data.materialDTOList.map(c => ({ ...c, countNum: c.stockInNumber })) : data.detailList.map(c => ({ ...c, countNum: c.outInventoryNum }))
      }
      this.noticeData.unshift(obj)
      if (this.timer) clearTimeout(this.timer)
      this.visible = true
      this.timer = setTimeout(() => {
        this.visible = false
        this.resetDlgData()
      }, 10000)
    },
    resetDlgData() {
      this.noticeData = []
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  height: 100vh;
  padding-top: 20px;
  box-sizing: border-box;
  position: relative;
  .btn-control {
    position: absolute;
    z-index: 999;
    top: 0;
    right: 0;
  }
  .control-btn {
    height: 70px;
    .el-button {
      margin: 0;
    }
    position: absolute;
    z-index: 999;
    top: 30px;
    right: 0;
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around;
  }
  ::v-deep .el-carousel {
    .el-carousel__container {
      height: calc(100vh - 20px);
    }
  }
}
</style>
