<template>
  <chart-card title="库存总额及平均库存" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：万元</span>
        <el-row style="margin: 0;height: 100%;width: 100%;">
          <el-col :span="20" style="height: 100%;">
            <div :id="refName" :ref="refName" style="height: 100%;" />
          </el-col>
          <el-col :span="4" style="height: 100%;">
            <div class="text-wrap">
              <div v-for="(item, index) in tableData" :key="index" class="item-container">
                <span class="title" :style="{color: item.color }">{{ item.name }}</span>
                <div><span class="label">总额：</span><span class="content">{{ item.total }}</span></div>
                <div><span class="label">平均：</span><span class="content">{{ item.average }}</span></div>
              </div>
            </div>
            <!-- <el-descriptions title="" :content-style="{textAlign: 'center'}" :label-style="{textAlign: 'center'}" :column="1" border>
              <el-descriptions-item v-for="(item, index) in tableData" :key="index" :label="item.name">{{ item.value }}</el-descriptions-item>
            </el-descriptions> -->
          </el-col>
        </el-row>
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryAmountChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'InventoryInfo',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'InventoryInfo',
      tableData: []
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryAmountChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const { series, xAxis } = data
              const totalItems = series.filter(c => c.name.indexOf('总额') !== -1)
              const averageItems = series.filter(c => c.name.indexOf('平均') !== -1)

              const colors = ['#2ec7c9', '#b6a2de', '#5ab1ef', '#ffb980', '#d87a80']
              const a = totalItems.map((c, i) => ({ ...c, itemStyle: { color: colors[i] }}))
              const b = averageItems.map((c, i) => ({ ...c, itemStyle: { color: colors[i] }}))

              this.tableData = b.map((item, index) => {
                return {
                  name: item.name.split('平均')[0],
                  color: item.itemStyle.color,
                  total: a[index].data.reduce((total, cur) => total + cur * 100, 0) / 100,
                  average: item.data.reduce((total, cur) => total + cur * 100, 0) / 100
                }
              })

              this.getCompleteOptions(null, [...a, ...b], xAxis)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    // 使用整数来进行加法
    addWithInteger(num1, num2) {
      const factor = 1000000 // 可以根据需要定义精度
      num1 = Math.round(num1 * factor)
      num2 = Math.round(num2 * factor)
      return (num1 + num2) / factor
    },
    /*
      * @legendData
      * @series
      * @xAxis
      */
    getCompleteOptions(legendData, series, xAxis) {
      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 4 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
.text-wrap {
  display: flex;
  flex-flow: column wrap;
  .item-container {
    margin-bottom: 10px;
    display: flex;
    flex-flow: column wrap;
    .title {
      font-size: 16px;
    }
    .label {
      font-size: 14px;
    }
    .content {
      font-size: 12px;
    }
  }
}
</style>

