<template>
  <chart-card title="进口/国产占比" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <span style="position: absolute;right: 0;top: -50px;font-size: 16px;font-weight: 700;">单位：元</span>
        <el-row style="margin: 0;height: 100%;width: 100%;">
          <el-col :span="16" style="height: 100%;">
            <div :id="refName" :ref="refName" style="height: 100%;" />
          </el-col>
          <el-col :span="6" style="height: 100%;">
            <div class="text-wrap">
              <div v-for="(item, index) in tableData" :key="index" class="item-container">
                <span class="title">{{ item.name }}</span>
                <div><span class="label" style="color: #0181E2;">国产：</span><span class="content">{{ item.val2 }}</span></div>
                <div><span class="label" style="color: #F20D0A;">进口：</span><span class="content">{{ item.val1 }}</span></div>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryPercentageChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'ImportedOrDomesticRatio',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'ImportedOrDomesticRatio',
      tableData: []
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryPercentageChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const { series, yAxis, percentage } = data
              const names = Object.keys(percentage)
              const _series = names.map(name => ({
                data: percentage[name],
                name,
                type: 'bar',
                stack: 'total',
                itemStyle: {
                  // 柱条的颜色
                  color: name === '进口' ? '#F20D0A' : '#0181E2'
                },
                label: {
                  show: true,
                  // 支持：top / left / right / bottom / inside / insideLeft / insideRight / insideTop / insideBottom / insideTopLeft / insideBottomLeft / insideTopRight / insideBottomRight
                  position: 'inside',
                  // {a}：系列名。
                  // {b}：数据名。
                  // {c}：数据值。
                  // {@xxx}：数据中名为 'xxx' 的维度的值，如 {@product} 表示名为 'product' 的维度的值。
                  // {@[n]}：数据中维度 n 的值，如 {@[3]} 表示维度 3 的值，从 0 开始计数。
                  formatter: `{c}%`,
                  // formatter: ({ data, seriesName, dataIndex }) => {
                  //   const num = data * 100
                  //   const sum = (series['国产'][dataIndex] * 100 + series['进口'][dataIndex] * 100)
                  //   const ratio = num / sum * 100
                  //   return `${ratio.toFixed(2)}%`
                  //   // return `${seriesName}：${ratio.toFixed(2)}%`
                  // },
                  emphasis: {
                    focus: 'series'
                  }
                  // emphasis: {
                  //   focus: 'series',
                  //   itemStyle: {
                  //     shadowBlur: 10, // 图形阴影的模糊大小。该属性配合 shadowColor,shadowOffsetX, shadowOffsetY 一起设置图形的阴影效果。
                  //     shadowOffsetX: 0,
                  //     shadowColor: 'rgba(0, 0, 0, 0.5)'
                  //   }
                  // }
                }
              }))
              // 拼装表格数据
              this.tableData = yAxis.map((c, i) => ({
                name: c,
                val1: series['进口'][i], // 进口
                val2: series['国产'][i] // 国产
              }))
              this.getCompleteOptions(null, _series, yAxis, series)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
      * @legendData
      * @series
      * @xAxis
      */
    getCompleteOptions(legendData, series, yAxis, extraInfo = []) {
      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // Use axis to trigger tooltip
            type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
          },
          formatter(params) {
            const arr = params.map(c => `<span style="display: inline-block;width: 10px;height: 10px;border-radius: 50%;background-color: ${c.seriesName === '进口' ? '#F20D0A' : '#0181E2'};margin-right: 5px;"></span>${c.seriesName}：<span style="font-weight: bold;">${extraInfo[c.seriesName][c.dataIndex]}</span>`)
            return `${params[0].name}<br />${arr.join('<br />')}`
          }
        },
        legend: {
          data: legendData,
          left: 'center'
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category',
          data: yAxis,
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 2 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber) // 计算展示行数
              if (paramsNameNumber > provideNumber) { // 如果超过1行
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            }
          }
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
.text-wrap {
  display: flex;
  flex-flow: column wrap;
  .item-container {
    margin-bottom: 10px;
    display: flex;
    flex-flow: column wrap;
    .title {
      font-size: 16px;
    }
    .label {
      font-size: 14px;
    }
    .content {
      font-size: 12px;
    }
  }
}
</style>

