<template>
  <chart-card title="库存周转率" class="chart-card">
    <template>
      <div v-loading="loading" style="height: 100%">
        <div :id="refName" :ref="refName" style="height: 100%;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'
import { getInventoryTurnoverChartApi } from '@/apis/modules/panel/zhongyuan'

export default {
  name: 'InventoryCarryRate',
  components: {
    ChartCard
  },
  mixins: [zoom],
  data() {
    return {
      refName: 'InventoryCarryRate'
    }
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
    this.resize()
    this.init()
    this.resize()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getInventoryTurnoverChartApi()
          .then(res => {
            const { code, data } = res
            if (!code) {
              const series = data.series.map(c => ({
                ...c,
                data: c.data.map(num => num.toFixed(2)),
                label: {
                  show: true, // 显示标签
                  position: 'inside', // 在饼图内部显示标签
                  formatter: params => `${Number(params.value).toFixed(1)}` // 定义标签的显示格式，{b}代表数据项名称，{d}代表百分比
                }
              }))
              const xAxis = data.xAxis
              this.getCompleteOptions(null, series, xAxis)
              this.loading = false
            }
            resolve(res)
          })
          .catch(err => {
            this.loading = false
            reject(err)
          })
      })
    },
    /*
        * @legendData
        * @series
        * @xAxis
        */
    getCompleteOptions(legendData, series, xAxis) {
      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          //   left: 0
          left: 'center'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            },
            interval: 0.2
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0
          },
          data: xAxis
        },
        series
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      chart && this.chart.setOption(this.option)
      this.chart && this.chart.resize()
    }
  }
}
</script>

<style lang='scss' scoped>
.chart-card {
  ::v-deep .el-card__body {
    // 减去面板头部高度、内容内边距占据的高度、额外富裕高度(防止x轴label展示不全)
    height: calc(100% - 34px - 20px - 5px);
  }
}
</style>

